.App {
  font-family: sans-serif;
  text-align: center;
}
.slack,
.twitter {
  display: none;
}
.name {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.button {
  padding: 18px 2vw;
  color: black;
  width: 60%;
  border: none;
  margin-top: 20px;
}
.profile {
  height: 100px;
  width: 100px;
  border-radius: 50px;
  margin-top: 20px;
}
.foot1 {
  width: 180px;
  height: 20px;
}
.foot2 {
  padding-right: 55px;
  padding-left: 55px;
}
.bottom-img {
  margin-bottom: 30px;
  margin-top: 250px;
}
.icon1 {
  height: 25px;
  width: 25px;
  margin-right: 20px;
}
.icon2 {
  height: 25px;
  width: 25px;
}
.icon {
  margin-bottom: 30px;
  margin-top: 30px;
}
@media (max-width: 480px) {
  .bottom-img {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  .foot1 {
    width: 100px;
    height: 15px;
  }
  .foot2 {
    width: 150px;
    padding: 0;
    height: 10px;
  }
  .foot3 {
    width: 100px;
    height: 20px;
    padding: 0;
  }
  
}
.main{
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  box-sizing: border-box;
}
.body2{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}
.header{
  font-weight: 600;
font-size: 36px;
color: #101828;
font-family: 'Inter';
}
.para{
  color: #475467;
  font-weight: 400;
font-size: 16px;
}
form{
  color: #344054;
}
label{
  font-size: large;
  font-weight: 500;
  font-size: 14px;
}

.label1, .label2{
  display: inline-block;
  margin: 30px 0px;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
border-radius: 8px;
}
.label2{
  margin-left: 40px;
}
.firstname, .lastname {
  height: 25px;
  display: block;
  width: 380px;
  height: 44px;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
border-radius: 8px;
border: 1px solid #D0D5DD;
margin-top: 10px;
  
}
.Email{
  height: 44px;
  width: 100%;
  display: block;
  border: 1px solid #D0D5DD;
  margin-top: 10px;

box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
border-radius: 8px;

}
textarea{ 
  height:132px;
  width: 100%;
  display: block;
  border: 1px solid #D0D5DD;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
border-radius: 8px;
margin-top: 10px;


}
textarea::placeholder{
  font-size: 16px;
  line-height: 24px;
}

.label3, .label4 {
display: block;
text-align: left;
margin-bottom: 20px;

}
.checkbox{
  margin-left: 0px;
  width: 20px;
height: 20px;
border: 1px solid #D0D5DD;
border-radius: 10px;
}
.label5{
  color: #475467;
  height: 24px;
  font-size: 24px;
  font-weight: 400;
}
.submit{
  width: 100%;
  border: 1px solid #1570EF;
  height: 48px;
  margin-top: 30px;
 border-radius: 8px;
  background-color: #1570EF;
  color: white;
  font-size: 16px;
  font-family: 'Inter';
font-style: normal;
font-weight: 600;
line-height: 24px;
}
@media (max-width: 450px) {
  /*contact style*/
  .body2 {
    text-align: center;
  }
  .firstname, .lastname, .Email  {
    display: block;
    width: 343px;
    height: 44px;
  }
  textarea {
    display: block;
    width: 343px;
    height: 132px;
  }
  .para {
    font-size: 18px;
    line-height: 28px;
  }
  .label5 > p {
    font-size: 16px;
    display: inline;
  }
  .checkbox {
    display: inline-block;
    border: 1px solid #d0d5dd;
    border-radius: 6px;
  }
  .label5{
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items:center;
    gap:8px;
  }

  .label2 {
    margin-left: 0;
  }
}
